<template>
  <v-sheet width="65rem">
    <v-row v-if="isLoading">
      <v-col class="d-flex justify-center">
        <v-progress-circular
          color="secondary"
          size="65"
          width="7"
          indeterminate
        ></v-progress-circular>
      </v-col>
    </v-row>
    <v-row v-else-if="displayItems.length == 0 && !isLoading">
      <v-col>
        <v-alert
          dense
          outlined
          color="info"
          icon="mdi-information-variant"
          text
        >
          Sem estações disponíveis</v-alert
        >
      </v-col>
    </v-row>
    <v-row v-show="displayItems.length > 0 && !isLoading">
      <v-col cols="5">
        <v-sheet class="pa-2 mb-4" border rounded="lg">
          <v-form v-model="localFormValid">
            <v-autocomplete
              v-model="localWStoAdd"
              class="mt-4"
              label="Estações disponíveis"
              variant="outlined"
              color="secondary"
              :items="selectItems"
              item-title="displayName"
              :rules="rules"
              return-object
            ></v-autocomplete>
          </v-form>
          <div v-if="localWStoAdd != null">
            <v-divider></v-divider>
            <v-sheet class="mt-4">
              <span>
                <b>Fornecedor:</b>
                {{ localWStoAdd.dataprovider_name }}
              </span>
              <br />
              <span>
                <b>
                  Identificador
                  {{ localWStoAdd.dataprovider_name }}:
                </b>
                {{ localWStoAdd.datasource_externalId }}
              </span>
              <br />
              <br />
              <span>
                <b> Nome da Estação: </b>
                {{ localWStoAdd.datasource_name }}
              </span>
              <br />
              <span>
                <b>Longitude:</b>
                {{ localWStoAdd.location[0] }}
              </span>
              <br />
              <span>
                <b>Latitude:</b>
                {{ localWStoAdd.location[1] }}
              </span>
              <br />
            </v-sheet>
          </div>
        </v-sheet>
        <SeriesProvider
          v-if="localWStoAdd != null"
          :providerSelected="provider"
          :WStoAdd="localWStoAdd"
          :formValid="localFormValid"
          @update:WStoAdd="(value) => (localWStoAdd = value)"
          @update:formValid="(value) => (localFormValid = value)"
        ></SeriesProvider>
      </v-col>
      <v-col cols="7">
        <v-sheet class="pa-2" border rounded="lg">
          <MapboxMap
            ref="mapboxMap"
            :accessToken="accessToken"
            mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
            @mb-created="(mapInstance) => (map = mapInstance)"
            :center="mapCenter"
            :zoom="8"
            style="height: 40rem"
          >
          </MapboxMap>
        </v-sheet>
      </v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import SeriesProvider from "./SeriesProvider.vue";

import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddWisecropWeatherStation",
  components: {
    SeriesProvider,
    MapboxMap,
  },
  props: {
    WStoAdd: {
      type: Object,
    },
    provider: {
      type: Object,
    },
    providerAccount: {
      type: Object,
    },
    WSfromFitoAgro: {
      type: Object,
      default: () => ({
        type: "FeatureCollection",
        features: [],
      }),
    },
    formValid: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    const popup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { accountStore, generalStore, popup, accessToken };
  },
  data() {
    return {
      isLoading: true,
      map: null,
      mapCenter: [-9, 39],
      rules: [
        (v) => !!v || "Selecione uma estação",
        (v) => v !== null || "Selecione uma estação",
      ],
      WSfromWisecrop: [],
    };
  },
  watch: {
    localWStoAdd: {
      handler: function (newVal, oldVal) {
        if (newVal) {
          this.map.flyTo({
            center: newVal.location,
            zoom: 12,
          });
        }
      },
      deep: true,
    },
    providerAccount: {
      handler: function (newVal, oldVal) {
        this.recenterMap();
        if (newVal) {
          this.getWisecropWeatherStations();
        }
      },
    },
  },
  computed: {
    localWStoAdd: {
      get() {
        return this.WStoAdd;
      },
      set(value) {
        this.$emit("update:WStoAdd", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
    displayItems() {
      return this.WSfromWisecrop.filter((provStation) => {
        return !this.WSfromFitoAgro.features.some(
          (fitoStation) =>
            fitoStation.properties.datasource_externalId ===
            provStation.datasource_externalId
        );
      });
    },
    mapItems() {
      return {
        type: "FeatureCollection",
        features: this.displayItems.map((station) => {
          return {
            type: "Feature",
            geometry: station.geometry,
            properties: {
              datasource_name: station.datasource_name,
              datasource_externalId: station.datasource_externalId,
              dataprovider_id: station.dataprovider_id,
              dataprovider_name: station.dataprovider_name,
            },
          };
        }),
      };
    },
    selectItems() {
      return this.displayItems.map((station) => {
        return {
          displayName:
            station.datasource_name + " - " + station.datasource_externalId,
          dataprovider_id: station.dataprovider_id,
          dataprovider_name: station.dataprovider_name,
          datasource_externalId: station.datasource_externalId,
          datasource_name: station.datasource_name,
          location: station.geometry.coordinates,
        };
      });
    },
  },
  methods: {
    async getWisecropWeatherStations() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/wisecrop/" + this.providerAccount.agent_id + "/stations",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.WSfromWisecrop = response.data.map((station) => {
            return {
              geometry: {
                type: "Point",
                coordinates: station.coords,
              },
              datasource_name: station.name,
              datasource_externalId: station.id,
              dataprovider_id: this.provider.dataprovider_id,
              dataprovider_name: this.provider.dataprovider_name,
            };
          });
          this.reloadMapLayer();
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);

          this.isLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao buscar Estações da conta WiseCrop!"
          );
        });
    },
    reloadMapLayer() {
      this.map.getSource("weather-stations-provider").setData(this.mapItems);
    },
    recenterMap() {
      this.map.setCenter(this.mapCenter);
      this.map.setZoom(8);
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("weather-stations-provider", {
        type: "geojson",
        data: this.mapItems,
      });
      this.map.addLayer({
        id: "weather-stations-provider",
        type: "circle",
        source: "weather-stations-provider",
        paint: {
          "circle-radius": 6,
          "circle-color": "#B42222",
          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
      });
      this.map.on("click", "weather-stations-provider", (e) => {
        let coordinates = e.features[0].geometry.coordinates.slice();
        let externalId = e.features[0].properties.datasource_externalId;
        let name = e.features[0].properties.datasource_name;
        let dataprovider_id = e.features[0].properties.dataprovider_id;
        let dataprovider_name = e.features[0].properties.dataprovider_name;
        this.localWStoAdd = {
          displayName: name + " - " + externalId,
          dataprovider_id: dataprovider_id,
          dataprovider_name: dataprovider_name,
          datasource_externalId: externalId,
          datasource_name: name,
          location: coordinates,
        };
      });
      this.map.on("mouseenter", "weather-stations-provider", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let coordinates = e.features[0].geometry.coordinates.slice();
        let description =
          "<b>Local:</b> " +
          e.features[0].properties.datasource_name +
          "<br> <b>ID:</b> " +
          e.features[0].properties.datasource_externalId;
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "weather-stations-provider", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
      if (this.providerAccount) this.getWisecropWeatherStations();
    });
  },
};
</script>

<style></style>
