<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg">
    <v-data-table-virtual
      fixed-headers
      no-data-text="Nenhuma estação encontrada"
      loading-text="Carregando estações..."
      :headers="headers"
      :items="tableItems"
      :height="tableHeight"
    >
      <template v-slot:item="row">
        <tr>
          <td>
            <v-btn
              color="info"
              variant="text"
              icon="mdi-information-outline"
              @click="showInfo(row.item)"
            ></v-btn>
          </td>
          <td>
            {{ row.item.datasource_name }}
          </td>
          <td>
            <v-chip
              :color="row.item.dataprovider_name"
              variant="outlined"
              >{{ row.item.dataprovider_name }}</v-chip
            >
          </td>
          <td>{{ row.item.datasource_externalId }}</td>
          <td class="text-center">
            <v-btn
              color="info"
              variant="text"
              icon
              @click="showOnMap(row.item)"
            >
              <v-icon>mdi-crosshairs-gps</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >{{ row.item.location }}
              </v-tooltip></v-btn
            >
          </td>
          <td
            v-if="accountStore.hasRole(['Administrador'])"
            class="d-flex justify-space-evenly nowrap"
          >
            <v-btn
              color="warning"
              variant="text"
              icon
              @click="openEditDialog(row.item)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Editar
              </v-tooltip>
            </v-btn>
            <v-btn
              color="error"
              variant="text"
              icon
              @click="openRemoveDialog(row.item)"
            >
              <v-icon>mdi-delete-outline</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Remover
              </v-tooltip>
            </v-btn>
          </td>
        </tr>
      </template>
    </v-data-table-virtual>
    <v-dialog
      v-model="deleteDialog"
      @afterLeave="reset()"
      persistent
      width="auto"
    >
      <v-card width="32rem">
        <v-card-title class="bg-error d-flex align-center"
          >
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Remover Estação</span></v-card-title
        >
        <v-card-text>
          <v-row>
            <v-col cols="12">
              <v-text-field
                color="secondary"
                variant="outlined"
                readonly
                hide-details
                density="compact"
                v-model="station.datasource_name"
                label="Nome da estação"
                required
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                color="secondary"
                variant="outlined"
                readonly
                hide-details
                density="compact"
                v-model="station.dataprovider_name"
                label="Fornecedor"
                required
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                color="secondary"
                variant="outlined"
                readonly
                hide-details
                density="compact"
                v-model="station.datasource_externalId"
                label="Identificador"
                required
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            @click="deleteStation()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- <v-dialog
      v-model="editDialog"
      @afterLeave="reset()"
      persistent
      width="auto"
    >
      <v-card width="32rem">
        <v-card-title class="bg-secondary">Editar nome da estação</v-card-title>
        <v-card-text>
          <v-form v-model="editFormValid" lazy-validation>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  color="secondary"
                  variant="outlined"
                  density="compact"
                  v-model="station.datasource_name"
                  label="Nome da estação"
                  required
                  :rules="rules.edit.name"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="editDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            :disabled="!editFormValid"
            @click="editStation()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog> -->
    <WeatherStationDetail
      :stationOnFocus="station"
      :dialog="detailDialog"
      :editMode="editMode"
      @update:dialog="(value) => (detailDialog = value)"
      @update:WSfromFitoAgro="$emit('update:WSfromFitoAgro')"
    ></WeatherStationDetail>
  </v-sheet>
</template>

<script>
import { useWeatherStore } from "@/stores/weatherStore.js";
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import WeatherStationDetail from "./WeatherStationDetail.vue";

import axios from "axios";
import { ca } from "@mapbox/mapbox-gl-geocoder/lib/exceptions";
export default {
  name: "WeatherStationsTable",
  components: {
    WeatherStationDetail,
  },
  props: {
    tableItems: {
      type: Array,
      required: true,
    },
  },
  setup() {
    const weatherStore = useWeatherStore();
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { weatherStore, accountStore, generalStore };
  },
  data() {
    return {
      deleteDialog: false,
      detailDialog: false,
      editMode: false,
      editFormValid: false,
      isLoading: false,
      station: null,
      rules: {
        edit: {
          name: [(v) => !!v || "Insira o nome da estação"],
        },
      },
      // headers: [
      //   { text: "", value: "info" },
      //   { title: "Nome", key: "datasource_name" },
      //   { title: "Fornecedor", key: "dataprovider_name", order: "asc" },
      //   { title: "ID Fornecedor", key: "datasource_externalId" },
      //   { title: "Localização", align: "center" },
      //   { title: "Ações", align: "center" },
      // ],
    };
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 260;
    },
    headers() {
      let headers = [
        { text: "", value: "info" },
        { title: "Nome", key: "datasource_name" },
        { title: "Fornecedor", key: "dataprovider_name", order: "asc" },
        { title: "ID Fornecedor", key: "datasource_externalId" },
        { title: "Localização", align: "center" },
      ];
      if (this.accountStore.hasRole(["Administrador"])) {
        headers.push({ title: "Ações", align: "center" });
      }
      return headers;
    },
  },
  methods: {
    showOnMap(item) {
      this.weatherStore.WSonFocus = item;
    },
    showInfo(item) {
      this.station = Object.assign({}, item);
      this.editMode = false;
      this.detailDialog = true;
    },
    openRemoveDialog(item) {
      this.deleteDialog = true;
      this.station = Object.assign({}, item);
    },
    openEditDialog(item) {
      this.station = Object.assign({}, item);
      this.editMode = true;
      this.detailDialog = true;
    },
    deleteStation() {
      this.isLoading = true;
      console.log(this.station);
      axios
        .request({
          url:
            "/api/dataproviders/" +
            this.station.dataprovider_id +
            "/datasources/" +
            this.station.datasource_id,
          method: "delete",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoading = false;
          this.deleteDialog = false;
          this.generalStore.successSnackbar("Estação removida com sucesso");
          this.$emit("update:WSfromFitoAgro");
        })
        .catch((err) => {
          console.log(err.response);
          this.isLoading = false;
          this.generalStore.errorSnackbar(err.response.data.error, 3000);
        });
    },
    providerColor(provider) {
      switch (provider) {
        case "PESSL":
          return "#0a876a";
        case "APAS":
          return "#999900";
        case "IPMA":
          return "#33ccff";
        case "WiseCrop":
          return "#993300";
        case "TerraPro":
          return "#3366ff";
        default:
          return "grey";
      }
    },
    reset() {
      this.station = null;
    },
  },
};
</script>

<style></style>
