<template>
  <v-dialog v-model="mainDialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn
        v-bind="activatorProps"
        color="success"
        text="Nova estação"
        append-icon="mdi-plus"
      ></v-btn>
    </template>

    <template v-slot:default="{}">
      <v-stepper
        v-model="currStep"
        :items="steps"
        ref="stepper"
        width="auto"
        min-width="50rem"
      >
        <template v-slot:item.1>
          <v-row>
            <v-col cols="12" class="d-flex justify-center">
              <v-sheet rounded border class="pa-5" width="25rem">
                <v-form v-model="steps[0].formValid" lazy-validation>
                  <div>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Selecione um Fornecedor
                    </span>
                    <v-select
                      class="mt-4"
                      density="compact"
                      variant="outlined"
                      prepend-inner-icon="mdi-domain"
                      label="Fornecedor"
                      :items="selectItemsProvider"
                      :rules="steps[0].rules.provider"
                      item-title="dataprovider_name"
                      v-model="providerSelected"
                      return-object
                      required
                    ></v-select>
                  </div>
                  <div v-if="needsAccount">
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Seleciona uma Conta de Fornecedor
                    </span>
                    <v-select
                      class="mt-4"
                      density="compact"
                      variant="outlined"
                      prepend-inner-icon="mdi-account-outline"
                      label="Conta de Fornecedor"
                      no-data-text="Nenhuma conta disponível"
                      :loading="loadingAccounts"
                      :disabled="loadingAccounts"
                      :items="providerAccounts"
                      :rules="steps[0].rules.proAcc"
                      item-title="agent_username"
                      v-model="providerAccSelected"
                      return-object
                      required
                    ></v-select>
                  </div>
                </v-form>
              </v-sheet>
            </v-col>
          </v-row>
        </template>
        <template v-slot:item.2>
          <div v-if="providerNameSelected == 'PESSL'" class="mb-4">
            <AddPESSLWeatherStation
              :WStoAdd="WStoAdd"
              :providerSelected="providerSelected"
              :providerAccount="providerAccSelected"
              :WSfromFitoAgro="WSfromFitoagro"
              :formValid="steps[1].formValid"
              @update:WStoAdd="(value) => (WStoAdd = value)"
              @update:formValid="(value) => updateFormValid(1, value)"
            ></AddPESSLWeatherStation>
          </div>
          <div v-else-if="providerNameSelected == 'APAS'" class="mb-4">
            <AddAPASWeatherStation
              :WStoAdd="WStoAdd"
              :providerSelected="providerSelected"
              :providerAccount="providerAccSelected"
              :WSfromFitoAgro="WSfromFitoagro"
              :formValid="steps[1].formValid"
              @update:WStoAdd="(value) => (WStoAdd = value)"
              @update:formValid="(value) => updateFormValid(1, value)"
            ></AddAPASWeatherStation>
          </div>
          <div v-else-if="providerNameSelected == 'IPMA'" class="mb-4 mr-4">
            <AddIPMAWeatherStation
              :WStoAdd="WStoAdd"
              :providerSelected="providerSelected"
              :WSfromFitoAgro="WSfromFitoagro"
              :formValid="steps[1].formValid"
              @update:WStoAdd="(value) => (WStoAdd = value)"
              @update:formValid="(value) => updateFormValid(1, value)"
            ></AddIPMAWeatherStation>
          </div>
          <div v-else-if="providerNameSelected == 'WiseCrop'">
            <AddWisecropWeatherStation
              :WStoAdd="WStoAdd"
              :provider="providerSelected"
              :providerAccount="providerAccSelected"
              :WSfromFitoAgro="WSfromFitoagro"
              :formValid="steps[1].formValid"
              @update:WStoAdd="(value) => (WStoAdd = value)"
              @update:formValid="(value) => updateFormValid(1, value)"
            ></AddWisecropWeatherStation>
          </div>
          <div v-else-if="providerNameSelected == 'TerraPro'">
            <AddTerraproWeatherStation
              :WStoAdd="WStoAdd"
              :provider="providerSelected"
              :providerAccount="providerAccSelected"
              :WSfromFitoAgro="WSfromFitoagro"
              :formValid="steps[1].formValid"
              @update:WStoAdd="(value) => (WStoAdd = value)"
              @update:formValid="(value) => updateFormValid(1, value)"
            ></AddTerraproWeatherStation>
          </div>
          <v-row v-else>
            <v-col cols="12">
              <v-alert dense outlined color="warning" icon="mdi-alert" text>
                Fornecedor não suportado
              </v-alert>
            </v-col>
          </v-row>
        </template>
        <template v-slot:item.3>
          <WSForm
            :station="WStoAdd"
            :formValid="steps[2].formValid"
            @update:station="(value) => (WStoAdd = value)"
            @update:formValid="(value) => updateFormValid(2, value)"
          ></WSForm>
        </template>
        <template v-slot:actions>
          <div class="d-flex justify-space-between pa-2">
            <v-btn
              prepend-icon="mdi-arrow-left"
              color="info"
              variant="tonal"
              @click="stepper.prev()"
              :disabled="currStep == 1"
              >Anterior</v-btn
            >
            <v-btn
              v-if="currStep < lastStep"
              color="info"
              variant="tonal"
              append-icon="mdi-arrow-right"
              :loading="isLoading"
              :disabled="!steps[currStep - 1].formValid"
              @click="stepper.next()"
              >Próximo</v-btn
            >
            <v-btn
              v-else
              color="success"
              variant="flat"
              :disabled="!steps[currStep - 1].formValid"
              @click="postWStoFitoAgro()"
              append-icon="mdi-check"
              >Adicionar</v-btn
            >
          </div>
        </template>
      </v-stepper>
    </template>
  </v-dialog>
</template>

<script>
import AddWeatherStationMap from "./AddWeatherStationMap.vue";
import AddPESSLWeatherStation from "./AddPESSLWeatherStation.vue";
import AddAPASWeatherStation from "./AddAPASWeatherStation.vue";
import AddIPMAWeatherStation from "./AddIPMAWeatherStation.vue";
import AddWisecropWeatherStation from "./AddWisecropWeatherStation.vue";
import AddTerraproWeatherStation from "./AddTerraproWeatherStation.vue";
import WSForm from "./WSForm.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useWeatherStore } from "@/stores/weatherStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

import { ref } from "vue";

export default {
  name: "AddWeatherStation",
  components: {
    AddWeatherStationMap,
    AddPESSLWeatherStation,
    AddAPASWeatherStation,
    AddIPMAWeatherStation,
    AddWisecropWeatherStation,
    AddTerraproWeatherStation,
    WSForm,
  },
  setup() {
    const accountStore = useAccountStore();
    const weatherStore = useWeatherStore();
    const generalStore = useGeneralStore();
    const stepper = ref(null);

    return { accountStore, weatherStore, generalStore, stepper };
  },
  data() {
    return {
      currStep: 1,
      providerSelected: null,
      providerAccSelected: null,
      WStoAdd: null,
      steps: [
        {
          title: "Fornecedor",
          rules: {
            provider: [(v) => !!v || "Selecione um fornecedor"],
            proAcc: [(v) => !!v || "Selecione uma conta"],
          },
          formValid: false,
        },
        {
          title: "Estações do Fornecedor",
          formValid: false,
        },
        {
          title: "Estação a Adicionar",
          formValid: false,
        },
      ],
      needsAccount: false,
      loadingAccounts: false,
      providerAccounts: [],
      WSfromFitoagro: {
        type: "FeatureCollection",
        features: [],
      },
      WSfromProvider: {
        type: "FeatureCollection",
        features: [],
      },
      map: null,
      mapCenter: [-9, 39],
      isLoading: false,
      mainDialog: false,
      confirmDialog: false,
    };
  },
  watch: {
    providerSelected: {
      handler: function (newVal, oldVal) {
        if (newVal) {
          this.WStoAdd = null;
          this.providerAccSelected = null;
          this.loadWSfromFitoagro(newVal.dataprovider_id);
          switch (this.providerSelected.dataprovider_name) {
            case "PESSL":
              this.needsAccount = true;
              this.loadProviderAccounts(this.providerSelected.dataprovider_id);
              this.steps = [
                {
                  title: "Fornecedor",
                  rules: {
                    provider: [(v) => !!v || "Selecione um fornecedor"],
                    proAcc: [(v) => !!v || "Selecione uma conta"],
                  },
                  formValid: false,
                },
                {
                  title: "Estações da Conta PESSL",
                  formValid: false,
                },
                {
                  title: "Estação a Adicionar",
                  formValid: false,
                },
              ];
              break;
            case "APAS":
              this.needsAccount = true;
              this.loadProviderAccounts(this.providerSelected.dataprovider_id);
              this.steps = [
                {
                  title: "Fornecedor",
                  rules: {
                    provider: [(v) => !!v || "Selecione um fornecedor"],
                    proAcc: [(v) => !!v || "Selecione uma conta"],
                  },
                  formValid: false,
                },
                {
                  title: "Estações da conta APAS disponíveis",
                  formValid: false,
                },
                {
                  title: "Estação a Adicionar",
                  formValid: false,
                },
              ];
              break;
            case "IPMA":
              this.needsAccount = false;
              this.steps = [
                {
                  title: "Fornecedor",
                  rules: {
                    provider: [(v) => !!v || "Selecione um fornecedor"],
                    proAcc: [(v) => !!v || "Selecione uma conta"],
                  },
                  formValid: false,
                },
                {
                  title: "Estações IPMA disponíveis",
                  formValid: false,
                },
                {
                  title: "Estação a Adicionar",
                  formValid: false,
                },
              ];
              break;
            case "WiseCrop":
              this.needsAccount = true;
              this.loadProviderAccounts(this.providerSelected.dataprovider_id);
              this.steps = [
                {
                  title: "Fornecedor",
                  rules: {
                    provider: [(v) => !!v || "Selecione um fornecedor"],
                    proAcc: [(v) => !!v || "Selecione uma conta"],
                  },
                  formValid: false,
                },
                {
                  title: "Estações da conta WiseCrop disponíveis",
                  formValid: false,
                },
                {
                  title: "Estação a Adicionar",
                  formValid: false,
                },
              ];
              break;
            case "TerraPro":
              this.needsAccount = true;
              this.loadProviderAccounts(this.providerSelected.dataprovider_id);
              this.steps = [
                {
                  title: "Fornecedor",
                  rules: {
                    provider: [(v) => !!v || "Selecione um fornecedor"],
                    proAcc: [(v) => !!v || "Selecione uma conta"],
                  },
                  formValid: false,
                },
                {
                  title: "Estações da conta TerraPro disponíveis",
                  formValid: false,
                },
                {
                  title: "Estação a Adicionar",
                  formValid: false,
                },
              ];
              break;
            default:
              this.steps = [
                {
                  title: "Fornecedor",
                  rules: {
                    provider: [(v) => !!v || "Selecione um fornecedor"],
                    proAcc: [(v) => !!v || "Selecione uma conta"],
                  },
                  formValid: false,
                },
                {
                  title: "Estações do Fornecedor",
                  formValid: false,
                },
                {
                  title: "Estação a Adicionar",
                  formValid: false,
                },
              ];
          }
          // this.weatherStore.providerToAdd = newVal;
        }
      },
      deep: true,
    },
    providerAccSelected: {
      handler: function (newVal, oldVal) {
        this.WStoAdd = null;
      },
    },
  },
  computed: {
    selectItemsProvider() {
      return this.weatherStore.providersFromFitoagro.filter(
        (item) =>
          item.dataprovider_name !== "ALL" &&
          item.dataprovider_name !== "SYSTEM"
      );
    },
    providerIdSelected() {
      return this.providerSelected.dataprovider_id;
    },
    providerNameSelected() {
      return this.providerSelected.dataprovider_name;
    },
    lastStep() {
      return this.steps.length;
    },
  },
  methods: {
    getMetadataByProvider(provider) {
      switch (provider) {
        case "IPMA":
          return {
            Timezone: "Europe/Lisbon",
            "Daylight offset": 0,
            "Standard offset": 0,
          };
        case "PESSL":
          return {
            Timezone: "Europe/Lisbon",
            "Daylight offset": 0,
            "Standard offset": 0,
          };
        case "APAS":
          return {
            Timezone: "Europe/Lisbon",
            "Daylight offset": 1,
            "Standard offset": 0,
          };
        case "WiseCrop":
          return {
            Timezone: "Europe/Lisbon",
            "Daylight offset": 1,
            "Standard offset": 0,
          };
        case "TerraPro":
          return {
            Timezone: "Europe/Lisbon",
            "Daylight offset": -1,
            "Standard offset": 0,
          };
      }
    },
    postWStoFitoAgro() {
      this.isLoading = true;
      let payload = {
        dataprovider_id: this.WStoAdd.dataprovider_id,
        datasource_externalid: this.WStoAdd.datasource_externalId,
        datasource_name: this.WStoAdd.datasource_name,
        datasource_location: {
          type: "Point",
          coordinates: this.WStoAdd.location,
        },
        datasource_metadata: this.getMetadataByProvider(
          this.WStoAdd.dataprovider_name
        ),
        datasource_properties: null,
        seriesvariables: this.WStoAdd.seriesvariables,
        datasourcetype_id: 1,
      };
      console.log(payload);
      axios
        .request({
          url:
            "api/dataproviders/" +
            this.WStoAdd.dataprovider_id +
            "/datasources",
          method: "post",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.mainDialog = false;
          this.isLoading = false;
          this.generalStore.successSnackbar("Estação adicionada com sucesso!");
          this.$emit("updateWS");
        })
        .catch((error) => {
          console.log(error);
          this.generalStore.errorSnackbar("Erro ao adicionar estação!");
          this.isLoading = false;
        });
    },

    async loadWSfromFitoagro(provider_Id) {
      axios
        .request({
          url: "api/dataproviders/" + provider_Id + "/datasources",
          method: "get",
          params: {
            datasourcetype_id: 1,
          },
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.WSfromFitoagro = {
            type: "FeatureCollection",
            features: response.data.datasources.map((item) => ({
              type: "Feature",
              properties: {
                datasource_id: item.datasource_id,
                datasource_name: item.datasource_name,
                dataprovider_id: item.dataprovider_id,
                datasource_externalId: item.datasource_externalid,
              },
              geometry: item.datasource_location,
            })),
          };
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async loadWSfromProvider(provider_name) {
      this.loadWSfromFitoagro(provider_Id);
      if (provider_name != "IPMA") {
        this.needsAccount = true;
        this.loadProviderAccounts(provider_Id);
      } else {
        this.needsAccount = false;
      }
    },
    async loadProviderAccounts(dataprovider_id) {
      this.loadingAccounts = true;
      axios
        .request({
          url: "api/providersaccounts",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            dataprovider_id: dataprovider_id,
          },
        })
        .then((response) => {
          this.providerAccounts = response.data.agents;
          this.loadingAccounts = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    valid(step) {
      switch (step) {
        case 1:
          return !this.steps.step1.formValid;
        case 2:
          return !this.steps.step2.formValid;
        case 3:
          return !this.steps.step3.formValid;
      }
    },
    updateFormValid(step, value) {
      this.steps[step].formValid = value;
    },
    reset() {
      console.log("reset");
      this.currStep = 1;
      this.needsAccount = false;
      this.loadingAccounts = false;
      this.providerAccounts = [];
      this.providerSelected = null;
      this.providerAccSelected = null;
      this.WStoAdd = null;
      // this.weatherStore.resetProviderToAdd();
      this.WSfromFitoagro = {
        type: "FeatureCollection",
        features: [],
      };
      this.WSfromProvider = {
        type: "FeatureCollection",
        features: [],
      };
    },
  },
};
</script>

<style scoped></style>
