<template>
  <v-card class="pa-2" elevation="0" border rounded="lg">
    <v-card-title class="d-flex align-center pl-2">
      <v-btn color="info" variant="text" icon @click="selectAll()">
        <v-icon>mdi-playlist-check</v-icon>
        <v-tooltip activator="parent" location="bottom"
          >Selecionar todos</v-tooltip
        > </v-btn
      ><span>Informação a Recolher</span>
    </v-card-title>
    <v-card-text class="scrollable-card">
      <div v-if="isLoading">
        <v-skeleton-loader
          v-for="n in 5"
          :key="n"
          type="heading"
        ></v-skeleton-loader>
      </div>
      <div v-else>
        <v-switch
          v-for="variable in seriesVariables"
          :key="variable.seriesvariable_id"
          v-model="localWStoAdd.seriesvariables"
          :label="variable.seriesvariable_longname"
          :value="variable"
          color="secondary"
          density="comfortable"
          hide-details
        ></v-switch></div
    ></v-card-text>
  </v-card>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "SeriesProvider",
  props: {
    providerSelected: {
      type: Object,
    },
    WStoAdd: {
      type: Object,
    },
    formValid: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    return { accountStore };
  },
  data() {
    return {
      series: [],
      seriesVariables: [],
      isLoading: false,
    };
  },
  watch: {
    providerSelected: {
      handler() {
        this.localWStoAdd.seriesvariables = [];
      },
      deep: true,
    },
  },
  computed: {
    localWStoAdd: {
      get() {
        return this.WStoAdd;
      },
      set(value) {
        this.$emit("update:WStoAdd", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
    providerName() {
      return this.providerSelected.dataprovider_name;
    },
  },
  methods: {
    getSeriesVariables(provider_id) {
      this.isLoading = true;
      axios
        .request({
          url: "api/dataprovider/" + provider_id + "/seriesvariables",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.seriesVariables = response.data;
          this.isLoading = false;
          // this.localWStoAdd.seriesvariables = response.data;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
        });
    },
    selectAll() {
      this.localWStoAdd.seriesvariables = this.seriesVariables;
    },
  },
  mounted() {
    this.localWStoAdd.seriesvariables = [];
    this.getSeriesVariables(this.providerSelected.dataprovider_id);
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 19rem;
  overflow-y: auto;
}
</style>
