<template>
  <v-sheet width="65rem">
    <v-row v-if="isLoading">
      <v-col class="d-flex justify-center">
        <v-progress-circular
          color="secondary"
          size="65"
          width="7"
          indeterminate
        ></v-progress-circular>
      </v-col>
    </v-row>
    <v-row v-else-if="displayItems.length == 0 && !isLoading">
      <v-col cols="12">
        <v-alert
          v-if="selectItemsWS.length == 0 && !isLoading"
          dense
          outlined
          color="info"
          icon="mdi-information-variant"
          border="left"
          text
        >
          Sem estações disponíveis</v-alert
        >
      </v-col>
    </v-row>
    <v-row v-show="displayItems.length > 0 && !isLoading">
      <v-col cols="5">
        <v-sheet class="pa-2 mb-4" border rounded="lg">
          <v-form v-model="localFormValid">
            <v-autocomplete
              v-model="localWStoAdd"
              class="mt-4"
              label="Estações disponíveis"
              placeholder="Selecione uma estação"
              no-data-text="Sem resultados"
              variant="outlined"
              :items="selectItemsWS"
              item-title="displayName"
              :rules="rules"
              return-object
            >
            </v-autocomplete>
          </v-form>
          <div v-if="localWStoAdd != null">
            <v-divider></v-divider>
            <v-sheet class="mt-4">
              <span>
                <b>Fornecedor:</b>
                {{ localWStoAdd.dataprovider_name }}
              </span>
              <br />
              <span>
                <b>
                  Identificador
                  {{ localWStoAdd.dataprovider_name }}:
                </b>
                {{ localWStoAdd.datasource_externalId }}
              </span>
              <br />
              <br />
              <span>
                <b> Localização: </b>
                {{ localWStoAdd.datasource_name }}
              </span>
              <br />
              <span>
                <b>Longitude:</b>
                {{ localWStoAdd.location[0] }}
              </span>
              <br />
              <span>
                <b>Latitude:</b>
                {{ localWStoAdd.location[1] }}
              </span>
              <br />
            </v-sheet>
          </div>
        </v-sheet>
        <SeriesProvider
          v-if="localWStoAdd != null"
          :providerSelected="providerSelected"
          :WStoAdd="localWStoAdd"
          :formValid="localFormValid"
          @update:WStoAdd="(value) => (localWStoAdd = value)"
          @update:formValid="(value) => (localFormValid = value)"
        ></SeriesProvider>
      </v-col>

      <v-col cols="7">
        <v-sheet class="pa-2" border rounded="lg">
          <MapboxMap
            ref="mapboxMap"
            :accessToken="accessToken"
            mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
            @mb-created="(mapInstance) => (map = mapInstance)"
            :center="mapCenter"
            :zoom="8"
            style="height: 40rem"
          >
          </MapboxMap>
        </v-sheet>
      </v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import SeriesProvider from "./SeriesProvider.vue";

import { useGeneralStore } from "@/stores/generalStore";

import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

import axios from "axios";
export default {
  name: "AddIPMAWeatherStation",
  components: { MapboxMap, SeriesProvider },

  props: {
    WStoAdd: {
      type: Object,
    },
    providerSelected: {
      type: Object,
      default: () => ({
        dataprovider_id: 0,
        dataprovider_name: "",
      }),
    },

    WSfromFitoAgro: {
      type: Object,
      default: () => ({
        type: "FeatureCollection",
        features: [],
      }),
    },
    formValid: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const generalStore = useGeneralStore();

    const popup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { popup, generalStore, accessToken };
  },
  data() {
    return {
      isLoading: true,
      map: null,
      mapCenter: [-9, 39],
      rules: [
        (v) => !!v || "Selecione uma estação",
        (v) => v !== null || "Selecione uma estação",
      ],
      WSfromIPMA: [],
    };
  },
  watch: {
    localWStoAdd: {
      handler: function (newVal, oldVal) {
        if (newVal) {
          this.map.flyTo({
            center: newVal.location,
            zoom: 12,
          });
        }
      },
      deep: true,
    },
    // mapItems: {
    //   handler: function (newVal, oldVal) {
    //     console.log(newVal);
    //     if (
    //       this.map.getSource("weather-stations-provider") &&
    //       newVal.length > 0
    //     ) {
    //       this.reloadMapLayer();
    //     }
    //   },
    // },
  },
  computed: {
    localWStoAdd: {
      get() {
        return this.WStoAdd;
      },
      set(value) {
        this.$emit("update:WStoAdd", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
    displayItems() {
      return this.WSfromIPMA.filter((provFeature) => {
        return !this.WSfromFitoAgro.features.some(
          (fitoFeature) =>
            parseInt(fitoFeature.properties.datasource_externalId) ===
            parseInt(provFeature.datasource_externalId)
        );
      });
    },
    mapItems() {
      return {
        type: "FeatureCollection",
        features: this.displayItems.map((ws) => {
          return {
            type: "Feature",
            geometry: ws.geometry,
            properties: {
              datasource_name: ws.datasource_name,
              datasource_externalId: ws.datasource_externalId,
              dataprovider_id: ws.dataprovider_id,
              dataprovider_name: ws.dataprovider_name,
            },
          };
        }),
      };
    },
    selectItemsWS() {
      return this.displayItems.map((ws) => {
        return {
          displayName: ws.datasource_name + " - " + ws.datasource_externalId,
          dataprovider_id: ws.dataprovider_id,
          dataprovider_name: ws.dataprovider_name,
          datasource_externalId: ws.datasource_externalId,
          datasource_name: ws.datasource_name,
          location: ws.geometry.coordinates,
        };
      });
    },
  },
  methods: {
    async getIPMAStations() {
      axios
        .get(
          "https://api.ipma.pt/open-data/observation/meteorology/stations/stations.json"
        )
        .then((response) => {
          this.WSfromIPMA = response.data.map((ws) => {
            return {
              geometry: ws.geometry,
              datasource_name: ws.properties.localEstacao,
              datasource_externalId: ws.properties.idEstacao,
              dataprovider_id: this.providerSelected.dataprovider_id,
              dataprovider_name: this.providerSelected.dataprovider_name,
            };
          });
          this.reloadMapLayer();
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao buscar Estações IPMA!");
        });
    },
    reloadMapLayer() {
      this.map.getSource("weather-stations-provider").setData(this.mapItems);
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("weather-stations-provider", {
        type: "geojson",
        data: this.mapItems,
      });
      this.map.addLayer({
        id: "weather-stations-provider",
        type: "circle",
        source: "weather-stations-provider",
        paint: {
          "circle-radius": 6,
          "circle-color": "#B42222",
          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
      });
      this.map.on("click", "weather-stations-provider", (e) => {
        let coordinates = e.features[0].geometry.coordinates.slice();
        let externalId = e.features[0].properties.datasource_externalId;
        let name = e.features[0].properties.datasource_name;
        let dataprovider_id = e.features[0].properties.dataprovider_id;
        let dataprovider_name = e.features[0].properties.dataprovider_name;
        this.localWStoAdd = {
          displayName: name + " - " + externalId,
          dataprovider_id: dataprovider_id,
          dataprovider_name: dataprovider_name,
          datasource_externalId: externalId,
          datasource_name: name,
          location: coordinates,
        };
      });
      this.map.on("mouseenter", "weather-stations-provider", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let coordinates = e.features[0].geometry.coordinates.slice();
        let description =
          "<b>Local:</b> " +
          e.features[0].properties.datasource_name +
          "<br> <b>ID:</b> " +
          e.features[0].properties.datasource_externalId;
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "weather-stations-provider", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
      this.getIPMAStations();
    });
  },
};
</script>

<style></style>
