<template>
  <v-dialog v-model="localDialog" @aferleave="onCloseDialog()" width="auto">
    <v-card width="500" height="600">
      <v-card-title :class="editMode ? 'bg-warning' : 'bg-info'">
        <div v-if="editMode">
          <v-icon size="small" class="mr-2">mdi-pencil</v-icon>
          <span>Editar Estação</span>
        </div>
        <div v-else>
          <v-icon size="small" class="mr-2">mdi-information-outline</v-icon>
          <span>Detalhes da Estação</span>
        </div>
      </v-card-title>
      <v-card-text class="pb-1">
        <v-container :class="{ 'fill-height': isLoading }">
          <v-row v-if="isLoading" align="center">
            <v-col cols="12" class="d-flex align-center justify-center"
              ><v-progress-circular
                color="secondary"
                size="65"
                width="7"
                indeterminate
              ></v-progress-circular>
            </v-col>
          </v-row>
          <v-form v-else-if="!isLoading && station != null" v-model="formValid">
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model.trim="station.datasource_name"
                  :rules="rules.name"
                  :readonly="!editMode"
                  color="secondary"
                  label="Nome da Estação"
                  prepend-icon="mdi-tag-text-outline"
                  density="compact"
                  variant="outlined"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6">
                <v-text-field
                  v-model="station.dataprovider.dataprovider_name"
                  readonly
                  :disabled="editMode"
                  color="secondary"
                  label="Fornecedor"
                  prepend-icon="mdi-domain"
                  density="compact"
                  variant="outlined"
                  hide-details
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  v-model="station.datasource_externalid"
                  readonly
                  :disabled="editMode"
                  color="secondary"
                  label="Identificador de Fornecedor"
                  prepend-icon="mdi-identifier"
                  density="compact"
                  variant="outlined"
                  hide-details
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-card
                  height="280"
                  class="pa-2"
                  elevation="0"
                  border
                  rounded="lg"
                >
                  <v-card-title class="d-flex align-center pl-2">
                    <v-btn
                      v-if="editMode"
                      color="info"
                      variant="text"
                      icon
                      @click="selectAll()"
                    >
                      <v-icon>mdi-playlist-check</v-icon>
                      <v-tooltip activator="parent" location="bottom"
                        >Selecionar todos</v-tooltip
                      > </v-btn
                    ><span>Informação a Recolher</span>
                    <StationData
                      v-if="!editMode"
                      :station_id="station.datasource_id"
                    ></StationData>
                  </v-card-title>
                  <v-card-text class="scrollable-card">
                    <v-switch
                      v-for="(item, index) in stationSeries"
                      :key="index"
                      v-model="item.active"
                      :label="item.seriesvariable_longname"
                      :readonly="!editMode"
                      color="secondary"
                      density="comfortable"
                      hide-details
                    ></v-switch>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions v-if="editMode" class="d-flex justify-space-around">
        <v-btn
          color="error"
          variant="tonal"
          icon="mdi-close"
          @click="localDialog = false"
        ></v-btn>
        <v-btn
          color="secondary"
          variant="tonal"
          icon="mdi-check"
          :loading="isLoadingEdit"
          :disabled="!formValid"
          @click="editStation()"
        ></v-btn
      ></v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import StationData from "./StationData.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "WeatherStationDetail",
  components: {
    StationData,
  },
  props: {
    stationOnFocus: {
      type: Object,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
    editMode: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoading: false,
      isLoadingEdit: false,
      formValid: false,
      station: null,
      seriesVariables: [],
      stationSeries: [],
      rules: {
        name: [
          (v) => !!v || "O nome é obrigatório",
          (v) =>
            (v && v.length <= 50) || "O nome deve ter menos de 50 caracteres",
        ],
      },
    };
  },
  watch: {
    dialog: {
      handler() {
        if (this.dialog) {
          this.onOpenDialog();
        }
      },
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
  },
  methods: {
    async getStationDetail() {
      let datasource_id = this.stationOnFocus.datasource_id;
      await axios
        .request({
          url: "api/datasources/" + datasource_id,
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.station = {
            ...response.data,
            seriesvariables: response.data.series.map((serie) => {
              return serie.seriesvariable_id;
            }),
          };
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getProviderSeries() {
      let provider_id = this.stationOnFocus.dataprovider_id;
      await axios
        .request({
          url: "api/dataprovider/" + provider_id + "/seriesvariables",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.seriesVariables = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    selectAll() {
      this.stationSeries = this.stationSeries.map((variable) => {
        return {
          ...variable,
          active: true,
        };
      });
    },
    async editStation() {
      this.isLoadingEdit = true;
      let payload = {
        // dataprovider_id: this.station.dataprovider.dataprovider_id,
        // datasourcetype_id: 1,
        datasource_name: this.station.datasource_name,
        // datasource_externalid: this.station.datasource_externalid,
        // datasource_location: this.station.datasource_location,
        // datasource_properties: this.station.datasource_properties,
        seriesvariables: this.stationSeries,
      };
      console.log(payload);
      await axios
        .request({
          url:
            "api/dataproviders/" +
            this.station.dataprovider.dataprovider_id +
            "/datasources/" +
            this.station.datasource_id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingEdit = false;
          this.localDialog = false;
          this.generalStore.successSnackbar("Estação editada com sucesso");
          this.$emit("update:WSfromFitoAgro");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao editar a estação");
        });
    },
    onOpenDialog() {
      this.isLoading = true;
      Promise.all([this.getProviderSeries(), this.getStationDetail()])
        .then(() => {
          this.stationSeries = this.seriesVariables.map((variable) => {
            return {
              ...variable,
              active: this.belongsToSeries(variable.seriesvariable_id),
            };
          });
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    onCloseDialog() {
      this.dialog = false;
      this.station = null;
      this.seriesVariables = [];
      this.stationSeries = [];
    },
    belongsToSeries(seriesvariable_id) {
      let serie = this.station.series.find(
        (serie) => serie.seriesvariable_id === seriesvariable_id
      );
      return serie ? true : false;
    },
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 200px;
  overflow-y: auto;
}
</style>
