<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-alert
        v-if="selectItemsWS.length == 0 && !isLoading"
        dense
        outlined
        color="info"
        icon="mdi-information-variant"
        text
      >
        Sem novas estações</v-alert
      >
      <v-sheet v-else rounded border class="pa-5" width="35rem">
        <v-form v-model="localFormValid">
          <v-autocomplete
            v-model="localWStoAdd"
            class="mt-4"
            label="Estações disponíveis"
            variant="outlined"
            density="compact"
            color="secondary"
            :items="selectItemsWS"
            :loading="isLoading"
            item-title="datasource_name"
            :rules="rules"
            return-object
          >
          </v-autocomplete>
        </v-form>
      </v-sheet>
    </v-col>
    <v-row>
      <v-col cols="12">
        <SeriesProvider
          v-if="localWStoAdd != null"
          :providerSelected="providerSelected"
          :WStoAdd="localWStoAdd"
          :formValid="localFormValid"
          @update:WStoAdd="(value) => (localWStoAdd = value)"
          @update:formValid="(value) => (localFormValid = value)"
        ></SeriesProvider>
      </v-col>
    </v-row>
  </v-row>
</template>

<script>
import SeriesProvider from "./SeriesProvider.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "AddAPASWeatherStation",
  components: {
    SeriesProvider,
  },
  props: {
    WStoAdd: {
      type: Object,
    },
    providerSelected: {
      type: Object,
      default: () => ({
        dataprovider_id: 0,
        dataprovider_name: "",
      }),
    },
    providerAccount: {
      type: Object,
    },
    WSfromFitoAgro: {
      type: Object,
      default: () => ({
        type: "FeatureCollection",
        features: [],
      }),
    },
    formValid: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      isLoading: false,
      WSfromAPAS: [],
      rules: [(v) => !!v || "Seleciona uma estação"],
    };
  },
  watch: {
    providerAccount: {
      handler: function (newVal, oldVal) {
        if (newVal) {
          this.getAPASWeatherStations();
        }
      },
    },
  },
  computed: {
    localWStoAdd: {
      get() {
        return this.WStoAdd;
      },
      set(value) {
        this.$emit("update:WStoAdd", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
    selectItemsWS() {
      return this.WSfromAPAS.filter((provFeature) => {
        return !this.WSfromFitoAgro.features.some(
          (fitoFeature) =>
            fitoFeature.properties.datasource_externalId ===
            provFeature.datasource_externalId
        );
      });
    },
  },
  methods: {
    async getAPASWeatherStations() {
      // let dropbox_code = this.providerAccount.api_auth.dropbox_code;
      // this.isLoading = true;
      // axios
      //   .request({
      //     url: "https://api.dropboxapi.com/2/files/list_folder",
      //     method: "post",
      //     headers: {
      //       Authorization: "Bearer " + dropbox_code,
      //     },
      //     data: {
      //       include_deleted: false,
      //       include_has_explicit_shared_members: false,
      //       include_media_info: false,
      //       include_mounted_folders: true,
      //       include_non_downloadable_files: true,
      //       path: "",
      //       recursive: false,
      //     },
      //   })
      axios
        .request({
          url: "api/apas/" + this.providerAccount.agent_id + "/stations",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.WSfromAPAS = response.data.entries.map((entry) => {
            return {
              dataprovider_id: this.providerSelected.dataprovider_id,
              dataprovider_name: this.providerSelected.dataprovider_name,
              datasource_externalId: entry.name,
              datasource_name: entry.name,
            };
          });
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao obter estações da conta APAS"
          );
        });
    },
  },
  mounted() {
    if (this.providerAccount) {
      this.getAPASWeatherStations();
    }
  },
};
</script>

<style></style>
