<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-sheet v-if="localStation" rounded border class="pa-5" width="35rem">
        <v-form ref="form" v-model="localFormValid">
          <v-row>
            <v-col cols="12">
              <v-text-field
                v-model.trim="localStation.datasource_name"
                :rules="rules.name"
                label="Nome da Estação"
                prepend-icon="mdi-pencil"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model="localStation.dataprovider_name"
                readonly
                label="Fornecedor"
                prepend-icon="mdi-domain"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="localStation.datasource_externalId"
                readonly
                label="Identificador de Fornecedor"
                prepend-icon="mdi-identifier"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-card class="pa-2" elevation="0" border rounded="lg">
                <v-card-title>Informação a recolher</v-card-title>
                <v-card-text class="scrollable-card">
                  <v-list>
                    <v-list-item
                      v-for="variable in localStation.seriesvariables"
                      :key="variable.seriesvariable_id"
                      :title="variable.seriesvariable_longname"
                    ></v-list-item>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-form>
      </v-sheet>
      <v-alert
        v-else
        dense
        outlined
        color="info"
        icon="mdi-information-variant"
        text
      >
        Sem estação selecionada</v-alert
      >
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "WSForm",
  props: {
    station: {
      type: Object,
    },
    formValid: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      rules: {
        name: [
          (v) => !!v || "Insira o nome da estação",
          (v) =>
            v.length <= 50 ||
            "O nome da estação deve ter menos de 50 caracteres",
        ],
      },
    };
  },
  computed: {
    localStation: {
      get() {
        return this.station;
      },
      set(value) {
        this.$emit("update:station", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 19rem;
  overflow-y: auto;
}
</style>
