<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg"
    ><MapboxMap
      ref="mapboxMap"
      :accessToken="accessToken"
      mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
      @mb-created="
        (mapInstance) => {
          map = mapInstance;
        }
      "
      :center="mapCenter"
      :zoom="8"
      :style="{ height: mapHeight + 'px' }"
    >
    </MapboxMap>
  </v-sheet>
</template>

<script>
import { useWeatherStore } from "@/stores/weatherStore.js";

import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

export default {
  name: "WeatherStationsMap",
  components: { MapboxMap },
  props: {
    WSfromFitoagro: {
      type: Object,
      default: () => ({
        type: "FeatureCollection",
        features: [],
      }),
    },
    // WSfromIPMA: {
    //   type: Object,
    //   default: () => ({
    //     type: "FeatureCollection",
    //     features: [],
    //   }),
    // },
  },
  setup() {
    const weatherStore = useWeatherStore();
    const popup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { weatherStore, popup, accessToken };
  },
  data() {
    return {
      map: null,
      mapCenter: [-9, 39],
    };
  },
  watch: {
    WSonFocus: {
      handler: function (newVal, oldVal) {
        if (newVal) {
          this.map.flyTo({
            center: newVal.location,
            zoom: 12,
          });
        }
      },
      deep: true,
    },
    WSfromFitoagro: {
      handler: function (newVal, oldVal) {
        let source = this.map.getSource("weather-stations-fitoagro");
        if (source) {
          source.setData(newVal);
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("weather-stations-fitoagro", {
        type: "geojson",
        data: this.WSfromFitoagro,
      });
      this.map.addLayer({
        id: "weather-stations-fitoagro",
        type: "circle",
        source: "weather-stations-fitoagro",
        paint: {
          "circle-radius": 6,
          "circle-color": [
            "match",
            ["get", "dataprovider_name"],
            "PESSL",
            "#0a876a",
            "APAS",
            "#999900",
            "IPMA",
            "#33ccff",
            "WiseCrop",
            "#993300",
            "TerraPro",
            "#3366ff",
            "#f0f",
          ],

          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
      });
      this.map.on("mouseenter", "weather-stations-fitoagro", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let coordinates = e.features[0].geometry.coordinates.slice();
        let description =
          "<b>Local:</b> " +
          e.features[0].properties.datasource_name +
          "<br> <b>Fornecedor:</b> " +
          e.features[0].properties.dataprovider_name;
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "weather-stations-fitoagro", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
      this.map.on("mouseenter", "weather-stations-IPMA", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let coordinates = e.features[0].geometry.coordinates.slice();
        let description =
          "<b>Local:</b> " +
          e.features[0].properties.localEstacao +
          "<br> <b>ID:</b> " +
          e.features[0].properties.idEstacao;
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "weather-stations-IPMA", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
    });
    this.$emit("loaded");
  },
  computed: {
    mapHeight() {
      return window.innerHeight - 260;
    },
    notWSfromFitoagro() {
      let _features = this.WSfromIPMA.features.filter((ipmaFeature) => {
        return !this.WSfromFitoagro.features.some((fitoFeature) => {
          return (
            parseInt(fitoFeature.properties.datasource_externalId) ===
            ipmaFeature.properties.idEstacao
          );
        });
      });
      return {
        type: "FeatureCollection",
        features: _features,
      };
    },
    WSonFocus() {
      return this.weatherStore.WSonFocus;
    },
  },
};
</script>

<style></style>
