<template>
  <v-container fluid class="fill-width">
    <v-row>
      <v-col cols="12">
        <v-card elevation="8" :loading="isLoading">
          <v-card-title>Filtro</v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="2">
                <v-select
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Fornecedor"
                  :disabled="isLoading"
                  :loading="isLoading"
                  :items="filterSelectItems"
                  item-title="dataprovider_name"
                  item-value="dataprovider_id"
                  v-model="filter.provider_Id"
                ></v-select>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Nome"
                  v-model="filter.datasource_name"
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="ID Fornecedor"
                  v-model="filter.datasource_externalId"
                ></v-text-field>
              </v-col>
              <v-col cols="1">
                <v-btn
                  color="secondary"
                  icon
                  variant="text"
                  @click="searchFitagro()"
                  ><v-icon size="x-large">mdi-magnify</v-icon></v-btn
                >
              </v-col>
              <v-col
                v-if="accountStore.hasRole(['Administrador'])"
                cols="5"
                class="d-flex justify-center"
              >
                <AddWeatherStation @updateWS="getWSfromFitoagro()" />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-show="!isLoading">
      <v-col cols="7">
        <WeatherStationsTable
          :tableItems="tableItems"
          @update:WSfromFitoAgro="getWSfromFitoagro()"
        />
      </v-col>
      <v-col cols="5">
        <WeatherStationsMap
          :WSfromFitoagro="mapIcons"
          @loaded="isLoading = false"
        />
      </v-col>
    </v-row>
    <v-row v-if="isLoading" align="center">
      <v-col class="d-flex align-center justify-center">
        <v-progress-linear
          color="secondary"
          size="65"
          width="7"
          indeterminate
        ></v-progress-linear>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import WeatherStationsTable from "@/components/WeatherStations/WeatherStationsTable.vue";
import WeatherStationsMap from "@/components/WeatherStations/WeatherStationsMap.vue";
import AddWeatherStation from "@/components/WeatherStations/AddWeatherStation/AddWeatherStation.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useWeatherStore } from "@/stores/weatherStore";
import axios from "axios";

export default {
  name: "WeatherStations",

  components: { WeatherStationsTable, WeatherStationsMap, AddWeatherStation },

  setup() {
    const accountStore = useAccountStore();
    const weatherStore = useWeatherStore();
    const WSfromFitoagro_id = 1;
    return { accountStore, weatherStore, WSfromFitoagro_id };
  },

  data() {
    return {
      isLoading: true,
      addWSdialog: false,
      WSfromFitoagro: {
        type: "FeatureCollection",
        features: [],
      },
      WSfromIPMA: {
        type: "FeatureCollection",
        features: [],
      },
      providersfromFitoAgro: [
        {
          dataprovider_id: 0,
          dataprovider_name: "ALL",
        },
      ],
      filter: {
        provider_Id: null,
        datasource_name: null,
        datasource_externalId: null,
      },
    };
  },
  methods: {
    stopLoading() {
      this.isLoading = false;
    },
    searchFitagro() {
      this.offset = 0;
      this.getWSfromFitoagro();
    },
    async getProvidersfromFitoAgro() {
      this.isLoading = true;
      axios
        .request({
          url: "api/dataproviders",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          response.data.forEach((item) => {
            this.providersfromFitoAgro.push({
              dataprovider_id: item.dataprovider_id,
              dataprovider_name: item.dataprovider_name,
            });
          });
          this.weatherStore.providersFromFitoagro = this.providersfromFitoAgro;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getWSfromFitoagro() {
      this.isLoading = true;
      let _provider_Id =
        this.filter.provider_Id != undefined ? this.filter.provider_Id : 0;
      // await new Promise((resolve) => setTimeout(resolve, 2000));

      axios
        .request({
          url: "api/dataproviders/" + _provider_Id + "/datasources",
          method: "get",
          params: {
            datasourcetype_id: this.WSfromFitoagro_id,
            datasource_name: this.filter.datasource_name,
            datasource_externalid: this.filter.datasource_externalId,
          },
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.WSfromFitoagro = {
            type: "FeatureCollection",
            features: response.data.datasources.map((item) => ({
              type: "Feature",
              properties: {
                datasource_id: item.datasource_id,
                datasourcetype_id: item.datasourcetype_id,
                datasource_name: item.datasource_name,
                dataprovider_id: item.dataprovider_id,
                datasource_externalId: item.datasource_externalid,
                datasource_properties: item.datasource_properties,
              },
              geometry: item.datasource_location,
            })),
          };
          this.weatherStore.WSfromFitoagro = this.WSfromFitoagro;
          this.offset += this.limit;
          this.isLoading = false;
          this.hasMore = response.data.hasMore;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {
    itemsToDisplay() {
      return this.WSfromFitoagro.features;
    },
    tableItems() {
      return this.itemsToDisplay.map((feature) => {
        return {
          datasource_id: feature.properties.datasource_id,
          datasourcetype_id: feature.properties.datasourcetype_id,
          datasource_name: feature.properties.datasource_name,
          dataprovider_id: feature.properties.dataprovider_id,
          dataprovider_name: this.providersfromFitoAgro.find(
            (provider) =>
              provider.dataprovider_id === feature.properties.dataprovider_id
          ).dataprovider_name,
          datasource_externalId: feature.properties.datasource_externalId,
          datasource_properties: feature.properties.datasource_properties,
          location: feature.geometry.coordinates,
        };
      });
    },
    mapIcons() {
      let _features = this.itemsToDisplay.map((feature) => {
        return {
          type: "Feature",
          properties: {
            datasource_id: feature.properties.datasource_id,
            datasource_name: feature.properties.datasource_name,
            dataprovider_id: feature.properties.dataprovider_id,
            dataprovider_name: this.providersfromFitoAgro.find(
              (provider) =>
                provider.dataprovider_id === feature.properties.dataprovider_id
            ).dataprovider_name,
            datasource_externalId: feature.properties.datasource_externalId,
          },
          geometry: feature.geometry,
        };
      });
      return {
        type: "FeatureCollection",
        features: _features,
      };
    },
    filterSelectItems() {
      return this.providersfromFitoAgro.filter(
        (item) =>
          item.dataprovider_name !== "ALL" &&
          item.dataprovider_name !== "SYSTEM"
      );
    },
  },
  watch: {
    // filter: {
    //   handler() {
    //     console.log(this.filter.provider_Id);
    //     this.getWSfromFitoagro(this.filter.provider_Id);
    //   },
    //   deep: true,
    // },
  },
  mounted() {
    this.isLoading = true;
    this.getProvidersfromFitoAgro().then(() => {
      this.getWSfromFitoagro();
    });
  },
  unmounted() {
    this.isLoading = true;
  },
};
</script>

<style></style>
