import { defineStore } from "pinia";

export const useWeatherStore = defineStore("weatherStore", {
  state: () => ({
    WSonFocus: {},
    WSfromFitoagro: {},
    providersFromFitoagro: {},
    WStoAdd: null,
    providerToAdd: null,
    providerAccountToAdd: null,
    authWiseCrop: {
      token: null,
      expeiration_date: null,
      cookies: null,
    }
  }),
  actions: {
    resetWStoAdd() {
      this.WStoAdd = null;
    },
    resetProviderToAdd() {
      this.providerToAdd = null;
      this.providerAccountToAdd = null;
    },
  },
});
