<template>
  <v-sheet class="" width="65rem">
    <v-row>
      <v-col cols="5">
        <v-sheet class="pa-2 mb-4" border rounded="lg">
          <v-form v-model="localFormValid">
            <v-autocomplete
              v-model="localWStoAdd"
              class="mt-4"
              label="Estações disponíveis"
              variant="outlined"
              color="secondary"
              :items="selectItemsWS"
              item-title="displayName"
              :rules="rules"
              return-object
            >
            </v-autocomplete>
          </v-form>
          <div v-if="WStoAdd != null">
            <v-divider></v-divider>
            <v-sheet class="mt-4">
              <span>
                <b>Fornecedor:</b>
                {{ WStoAdd.dataprovider_name }}
              </span>
              <br />
              <span>
                <b>
                  Identificador
                  {{ WStoAdd.dataprovider_name }}:
                </b>
                {{ WStoAdd.datasource_externalId }}
              </span>
              <br />
              <br />
              <span>
                <b> Localização: </b>
                {{ WStoAdd.datasource_name }}
              </span>
              <br />
              <!-- <span>
                <b>Cordenadas:</b>
                {{ WStoAdd.location }}
              </span> -->
              <span>
                <b>Longitude:</b>
                {{ WStoAdd.location[0] }}
              </span>
              <br />
              <span>
                <b>Latitude:</b>
                {{ WStoAdd.location[1] }}
              </span>
              <br />
            </v-sheet>
          </div>
        </v-sheet>
        <SeriesProvider
          v-if="WStoAdd != null"
          :providerSelected="providerSelected"
          :WStoAdd="localWStoAdd"
          :formValid="localFormValid"
          @update:WStoAdd="(value) => (WStoAdd = value)"
          @update:formValid="(value) => (formValid = value)"
        ></SeriesProvider>
      </v-col>

      <v-col cols="7">
        <v-sheet class="pa-2" border rounded="lg">
          <MapboxMap
            ref="mapboxMap"
            :accessToken="accessToken"
            mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
            @mb-created="(mapInstance) => (map = mapInstance)"
            :center="mapCenter"
            :zoom="8"
            style="height: 40rem"
          >
          </MapboxMap>
        </v-sheet>
      </v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import SeriesProvider from "./SeriesProvider.vue";

import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

export default {
  name: "AddWeatherStationMap",
  components: { MapboxMap, SeriesProvider },

  props: {
    WStoAdd: {
      type: Object,
    },
    formValid: {
      type: Boolean,
      default: false,
    },
    providerSelected: {
      type: Object,
      default: () => ({
        dataprovider_id: 0,
        dataprovider_name: "",
      }),
    },
    WSfromProvider: {
      type: Object,
      default: () => ({
        type: "FeatureCollection",
        features: [],
      }),
    },
  },
  setup() {
    const popup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { popup, accessToken };
  },
  data() {
    return {
      map: null,
      mapCenter: [-9, 39],
      rules: [
        (v) => !!v || "Selecione uma estação",
        (v) => v !== null || "Selecione uma estação",
      ],
    };
  },
  watch: {
    WStoAdd: {
      handler: function (newVal, oldVal) {
        if (newVal) {
          this.map.flyTo({
            center: newVal.location,
            zoom: 12,
          });
        }
      },
      deep: true,
    },
    WSfromProvider: {
      handler: function (newVal, oldVal) {
        if (this.map.getSource("weather-stations-provider"))
          this.map.getSource("weather-stations-provider").setData(newVal);
      },
      deep: true,
    },
  },
  computed: {
    localWStoAdd: {
      get() {
        return this.WStoAdd;
      },
      set(value) {
        this.$emit("update:WStoAdd", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
    selectItemsWS() {
      return this.WSfromProvider.features.map((item) => ({
        displayName:
          item.properties.datasource_name +
          " - " +
          item.properties.datasource_externalId,
        dataprovider_id: item.properties.dataprovider_id,
        dataprovider_name: item.properties.dataprovider_name,
        datasource_externalId: item.properties.datasource_externalId,
        datasource_name: item.properties.datasource_name,
        location: item.geometry.coordinates,
      }));
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("weather-stations-provider", {
        type: "geojson",
        data: this.WSfromProvider,
      });
      this.map.addLayer({
        id: "weather-stations-provider",
        type: "circle",
        source: "weather-stations-provider",
        paint: {
          "circle-radius": 6,
          "circle-color": "#B42222",
          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
      });
      this.map.on("click", "weather-stations-provider", (e) => {
        let coordinates = e.features[0].geometry.coordinates.slice();
        let externalId = e.features[0].properties.datasource_externalId;
        let name = e.features[0].properties.datasource_name;
        let dataprovider_id = e.features[0].properties.dataprovider_id;
        let dataprovider_name = e.features[0].properties.dataprovider_name;
        this.localWStoAdd = {
          displayName: name + " - " + externalId,
          dataprovider_id: dataprovider_id,
          dataprovider_name: dataprovider_name,
          datasource_externalId: externalId,
          datasource_name: name,
          location: coordinates,
        };
      });
      this.map.on("mouseenter", "weather-stations-provider", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let coordinates = e.features[0].geometry.coordinates.slice();
        let description =
          "<b>Local:</b> " +
          e.features[0].properties.datasource_name +
          "<br> <b>ID:</b> " +
          e.features[0].properties.datasource_externalId;
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "weather-stations-provider", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
    });
  },
};
</script>

<style></style>
